import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Subject } from 'rxjs';
import { ProductManagerService } from '../product-manager.service';
import { Router } from '@angular/router';
import { takeUntil, filter, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { FileUploaderService } from 'src/app/core/file-uploader.service';

@Component({
  selector: 'app-product-form',
  templateUrl: './product-form.component.html'
})
export class ProductFormComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  productForm: FormGroup;
  progressing = false;
  error: string;

  @Input()
  operation: 'CREATE' | 'UPDATE' = "CREATE";

  @Input()
  product: any;

  subcategoryList$ = this.productService.subcategoryList$;
  categoryList$ = this.productService.categoryList$;
 
  fileUploadError = '';

  constructor(private router: Router,
    private formBuilder: FormBuilder,
    private productService: ProductManagerService,
    private fileUploaderService: FileUploaderService) {
    // this.productService.getSubcategoryList();

  }
  // • Restaurant Name (No Product has same name)
  // • GST No (Optional)
  // • Contact Person Name
  // • Image
  // • Email (Optional)
  // • Mobile
  // • What’s App Number
  // • Password
  // • Confirm Password
  // • Address (Google Map)
  // • Area (Area)
  // • State
  // • Restaurant Category (Veg, Non Veg, Both)
  // • Default Rating
  // • Branches (Default Value - 1)
  // • Status (Default Active)
  ngOnInit(): void {
    console.log('product: ', this.product);
    this.productForm = this.formBuilder.group({
      product_id: this.product.product_id ,
      name: [this.product['name'], Validators.required],
      price: [this.product['price'], Validators.required],
      subcategory: [this.product['subcategory'], Validators.required],
      category: [this.product['category'], Validators.required],
      status: [this.product['status'], Validators.required],
      owner_id: [this.product['owner_id'], Validators.required],
      customizable: [this.product['customizable'], Validators.required],
      item_options: this.formBuilder.array(
        this.product && this.product.item_options ? this.product.item_options.map((item: any)=>
        this.createItem(item['option'], item['price'], item['default'])):[]),
        image: [this.product['image'], Validators.required],
    });


    this.productForm.valueChanges
    .pipe(distinctUntilChanged((a,b)=>JSON.stringify(a) == JSON.stringify(b)),debounceTime(500),takeUntil(this.ngUnsubcribe))
    .subscribe({
      next: (data: any) => {
        if(data.customizable) {
          this.productForm.get('price').setValue(data.item_options.find((item: any) => item.default && item.price));
        }
        console.log('data: ', data);
      },
      error: (data: any) => {
        console.log('data: ', data);
      }
    });
    this.productForm.get('category').valueChanges
      .pipe(takeUntil(this.ngUnsubcribe))
      .subscribe({
        next: (data: any) => {
          const category = typeof data === 'string' ? JSON.parse(data) : data;
          this.productService.getSubcategoryListForCategory(category['_id']);
          console.log('data: ', data, category);
        },
        error: (data: any) => {
          console.log('data: ', data);
        }
      });
  }
  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  addItem(): void {
    const item_options = this.productForm.get('item_options') as FormArray;
    item_options.push(this.createItem('', '', false));
  }
  createItem(option, price, default_): FormGroup {
    return this.formBuilder.group({
      option: [option, Validators.required],
      price: [price, Validators.required],
      default: [default_, Validators.required]
    });
  }
  
  submit(): void {
    this.progressing = true;
    console.log('Product Form Values: ', this.productForm.value);
    const product = this.productForm.value;
    try {
      product.subcategory = typeof product.subcategory === 'string' ? JSON.parse(product.subcategory) : product.subcategory;
      product.category = typeof product.category === 'string' ? JSON.parse(product.category) : product.category;

    } catch (error) {
      console.error(error);

    }
    if (this.operation == 'UPDATE') {
      this.productService.updateProduct(product)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/product/detail', this.product['product_id']]);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    } else {
      this.productService.createProduct(product)
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({
          next: (data: any) => {
            this.progressing = false;
            console.log('data: ', data);
            this.router.navigate(['/product']);
          },
          error: (data: any) => {
            console.log('data: ', data);
            this.progressing = false;
          }
        });
    }
  }
  uploadFile(fileList: FileList): void {
    console.log('FIle List: ', fileList);
    this.fileUploadError = '';
    const file = fileList[0];
    if (file) {
      if (!file.type.includes('image')) {
        this.fileUploadError = 'Please select an image file';
      }
      const reader = new FileReader();
      reader.onloadend = () => {
        const params = {
          file_name: this.productForm.get('name').value + '_' + file.name,
          file: btoa(reader.result as string),
          type: file.type
        }
        this.fileUploaderService.uploadFile(params)
          .pipe(
            filter((data: any) => data),
            takeUntil(this.ngUnsubcribe))
          .subscribe((data: any) => this.productForm.patchValue({image: data['image_url']}));

      }
      reader.readAsBinaryString(file);
    }

  }
}
