import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Subject, from } from 'rxjs';
import { ProductManagerService } from '../product-manager.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmDeleteComponent } from '../confirm-delete/confirm-delete.component';
import { filter, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html'
})
export class ProductListComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  // public productList$ = this.productManagerService.productList$;

  // public total$ = this.productManagerService.total$;
  // state = this.productManagerService.state;
  constructor(public productManagerService: ProductManagerService, private ModalService: NgbModal, private cdRef: ChangeDetectorRef) {
  }

  ngOnInit(): void {
    this.productManagerService.getProductList();
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }


  deleteProduct(product: any): void {
    const modalRef = this.ModalService.open(ConfirmDeleteComponent, {
      size: 'lg',
      backdrop: 'static'
    });
    modalRef.componentInstance.product = product;
    from(modalRef.result)
      .pipe(
        filter((data: any) => data),
        takeUntil(this.ngUnsubcribe))
      .subscribe(() => this.productManagerService.getProductList());
  }
  stateUpdate(): void {
    this.cdRef.detectChanges()
    console.log('fdf : ', this.productManagerService.state);
    
  }
}
