import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductManagerComponent } from './product-manager.component';
import { ProductListComponent } from './product-list/product-list.component';
import { CreateProductComponent } from './create/create-product.component';
import { EditProductComponent } from './edit/edit-product.component';
import { ProductDetailComponent } from './detail/product-detail.component';
import { ProductDetailResolver } from './detail/product-detail-resolver.service';


const routes: Routes = [  {
  path: '',
  component: ProductManagerComponent,
  children: [
    { path: '', redirectTo: 'list', pathMatch: 'full' },

    {
      path: 'list',
      component: ProductListComponent
    },
    {
      path: 'create',
      component: CreateProductComponent
    },
    {
      path: 'edit/:id',
      component: EditProductComponent,
      resolve: { product: ProductDetailResolver }
    },
    {
      path: 'detail/:id',
      component: ProductDetailComponent,
      resolve: { product: ProductDetailResolver }
    },
    { path: '**', redirectTo: 'list' }

  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [ProductDetailResolver]
})
export class ProductManagerRoutingModule { }
