import { Injectable } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { TableState } from '../core/table-state';
import { ProductService } from '../core/product.service';
import { AuthUserService } from '../core/auth-user.service';
import { SubcategoryService } from '../core/subcategory.service';
import { CategoryService } from '../core/category.service';

@Injectable()
export class ProductManagerService {
  public state: TableState = {
    page: 1,
    pageSize: 10,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  }

  private _total$ = new BehaviorSubject<number>(1);
  get total$(): Observable<number> {
    return this._total$.asObservable();
  }

private originalList = [];
  private _productList$ = new BehaviorSubject<any[]>(null);
  
  get productList$(): Observable<any[]> {
    return this._productList$.asObservable();
  }

  private _subcategoryList$ = new BehaviorSubject<any[]>(null);
  get subcategoryList$(): Observable<any[]> {
    return this._subcategoryList$.asObservable();
  }
  
  private _categoryList$ = new BehaviorSubject<any[]>(null);
  get categoryList$(): Observable<any[]> {
    return this._categoryList$.asObservable();
  }

  constructor(private productService: ProductService,
    private authUserService: AuthUserService,
    private subcategoryService: SubcategoryService,
    private categoryService: CategoryService) {
      this.getCategoryList();
    }



  createProduct(product: any): Observable<any> {
    return this.productService.createProduct(product);
  }

  updateProduct(product: any): Observable<any> {
    return this.productService.updateProduct(product);
  }

  deleteProduct(id: string): Observable<any> {
    return this.productService.deleteProduct(id);
  }

  getProduct(id: string): Observable<any> {
    return this.productService.getProduct(id);
  }


  getProductList(): void {
    this.productService.getProductList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Products for you: ', data);
        this._productList$.next(data);
        this.originalList = data;
        this._total$.next(data.length)
      });
  }

  getSubcategoryListForCategory(category_id: string): void {
    this.subcategoryService.getSubcategoryListForCategory(this.authUserService.user._id, category_id)
      .subscribe((data: any[]) => {
        console.log('Got Subcategories for you: ', data);
        this._subcategoryList$.next(data);
      });

  }
  getCategoryList(): void {
    this.categoryService.getCategoryList(this.authUserService.user._id)
      .subscribe((data: any[]) => {
        console.log('Got Subcategories for you: ', data);
        this._categoryList$.next(data);
      });
  }
  // name: req.body.name,
  // status: req.body.status,
  // cuisine: req.body.cuisine,
  // price: req.body.price,
  // subcategory: req.body.subcategory,
  // owner_id: req.body.owner_id
  searchText(): void {
    if (!this.state.searchTerm) {
      this._productList$.next(this.originalList);
      this._total$.next(this.originalList.length)
    } else {
      const searchTerm = this.state.searchTerm.toLocaleLowerCase();
      const filteredList = this.originalList.filter((item: any) => item['name'] && item['name'].toLocaleLowerCase().includes(searchTerm) ||
      item['category'] && item['category']['name'] && item['category']['name'].toLocaleLowerCase().includes(searchTerm) ||
      item['subcategory'] && item['subcategory']['name'] && item['subcategory']['name'].toLocaleLowerCase().includes(searchTerm) ||
      item['price'] && item['price'].toLocaleLowerCase().includes(searchTerm)
      );
      this._productList$.next(filteredList);
      this._total$.next(filteredList.length);
    }
  }
}
