import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { ProductManagerService } from '../product-manager.service';
import { empty } from 'rxjs';
import { catchError } from 'rxjs/operators';

@Injectable()
export class ProductDetailResolver implements Resolve<any> {
  constructor(private productManagerService: ProductManagerService) { }

  resolve(route: ActivatedRouteSnapshot, _state: RouterStateSnapshot) {
    console.log('router', route.params);
    return this.productManagerService.getProduct(route.params['id']).pipe(
      catchError((error) => {
        return empty();
      })
    );
  }
}
